import { LoadingController } from '@ionic/angular';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AuthService } from 'src/app/services/auth.service';
import { UserService } from 'src/app/services/user.service';
import { PlansService } from './../../services/plans.service';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.page.html',
  styleUrls: ['./profile.page.scss'],
})
export class ProfilePage implements OnInit {
  profile: any;
  nome: string;
  email: string;
  peso: number;
  altura: number;
  metas: any = [];
  programa: any = '';
  mesPrograma: any;

  constructor(
    private router: Router,
    private authSrv: AuthService,
    private userSrv: UserService,
    private planSrv: PlansService,
    private loadCtrl: LoadingController
  ) { }

  async ngOnInit() {
    setTimeout(async () => {
      await this.loadProfile();
    }, 2500); // Defina o tempo desejado em milissegundos (neste caso, 1000 ms ou 1 segundo)
  }

  onClick() {
    this.router.navigate(['tabs/tab1']);
  }

  async loadProfile() {
    await this.authSrv.profile().subscribe(data => {
      this.profile = data;
      this.nome = data.nome;
      this.email = data.email;

      this.loadUserData();
    });
  }

  async loadUserData() {
    const load = await this.loadCtrl.create({message:"Carregando"})
    load.present()
    await this.userSrv.getWeightUser().subscribe(data => {
      this.peso = data.dados.peso;
    });

    await this.userSrv.getHeightUser().subscribe(data => {
      this.altura = data.dados.altura;
    });

    await this.userSrv.getGoalsUser().subscribe(data => {
      this.metas = data.dados;
      console.log(this.metas.length);
    });

    await this.userSrv.getUserPlan().subscribe(data => {
      this.programa = data.programa;
      console.log(this.programa);
    });

    await this.planSrv.getMonthPlan().subscribe(data => {
      this.mesPrograma = data.mes_atual;
      console.log(this.mesPrograma);
    });
    load.dismiss()
  }

  goToAtualizaPeso() {
    this.router.navigate(['atualiza-peso']);
  }

}
